body {
	background-color: transparent;
}
.container {
	max-width: 600px !important;
}
.hiddenContainer {
	visibility: hidden;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.header-root {
	background-color: #fff;
	color: #000;
	border-top-right-radius: 8px;
	border-top-left-radius: 8px;
}
.minimize-window, .arrow-left {
	text-align: right;
	font-size: 24px;
	margin-top: 8px;
	margin-right: 16px;
	cursor: pointer;
}
.role-display {
	margin-bottom: 0px;
}
.avatar-container, .arrow-left {
	text-align: center;
}
.rp-sign {
	margin: -16px;
}
a {
	color: #340F7D;
}
.avatar {
	width: 48px;
	height: 48px;
	border-radius: 24px;
	margin-top: 8px;
}

.footer-root {
	position: fixed;
	left: 0;
	bottom: 0;
	height: 54px;
	width: 100%;
	background-color: #fff;
}
.footer-border {
	border: 1px solid #dedede;
}
.main-chat-field {
	width: 100%;
	padding: 18px;
	border: none;
}
.send-btn {
	font-size: 24px;
	color: #000;
	background-color: #fff;
	border: none;
	margin-top: 8px;
}
.powered {
	background-color: #dedede;
	font-size: 10px;
	border-bottom-right-radius: 8px;
	border-bottom-left-radius: 8px;
}
@media only screen and (max-device-width: 480px) { 
  .footer-root { 
    bottom: 0px;
  }
}
.img-card {
  	width: 100%;
}
.card-action {
	width: 100%;
}
.card-info {
	padding-left: 16px;
	padding-right: 16px;
	margin-top: 0px;
}
.card-holder {
	border-radius: 16px;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	width: 100%;
	height: 640px;
	z-index: 1;
}
.stack-1 {
	border-radius: 16px;
	background-color: #dedede;
	width: 85%;
    height: 400px;
    position: absolute;
    top: -8px;
    left: 24px;
    z-index: 0;
}
.stack-2 {
	border-radius: 16px;
	background-color: #dddddd4f;
	width: 75%;
    height: 400px;
    position: absolute;
    top: 0px;
    left: 36px;
}
.btn-no {
	background-color: red;
