	bottom: 60px;
	left: 40px;
	height: 64px;
	width: 64px;
	border-radius: 32px;
	color: #fff;
}
.btn-yes {
	background-color: green;
	position: fixed;
	bottom: 60px;
	right: 40px;
	height: 64px;
	width: 64px;
